body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  font-weight: 330 !important;
}

#main {
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

#mainNav {
  overflow: hidden;
  min-height: 100vh;
  height: 100vh;
}

#content {
  min-height: 100vh;
}

@media only screen and (min-width: 992px) {
  #content {
    height: 100vh;
    overflow: auto;
  }
}

@media only screen and (max-width: 991px) {
  body {
    overflow: auto; 
  }

  #mainNav {
    height: auto;
    overflow: hidden;
  }

  #content {
    height: auto;
    /*overflow: hidden;*/
  }
}

.Container {
  width: 100%;
}

.App-link {
  color: #61dafb;
}

.section-enter, .test-appear{
  opacity: 0;
}

.section-enter-active, .test-appear-active{
  opacity: 1;
  transition: opacity 1000ms;
}

.section-enter-done{
  opacity: 1;
}

.section-exit{
  opacity: 1;
}

.section-exit-active{
  opacity: 0;
  transition: opacity 500ms;
}

.section-exit-done{
  opacity: 0;
}

.nav-btn:hover {
  font-weight: 500;
  text-shadow: 0px 0px 15px #ccc;
}

#horizontalNav {
  transition: all 0.2s ease-in-out;
  position: fixed;
  left: 0;
  background: rgba(256, 256, 256, 0.9);
  z-index: 1;
}

.horizontalNav-show {
  top: 0;
  box-shadow: 0 0 16px rgba(0, 0, 0, .6);
  visibility: visible;
}

.horizontalNav-hide {
  transform: translateY(-100%);
  top: 0;
  visibility: hidden;
}